.app {
  width: 100vw;
  max-width: 100%;
}

.cover {
  width: 100%;
  height: 100vh;
  z-index: 1000;
  background-color: black;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
}

.parallax-text {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 20;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  line-height: 1.5;
}

.parallax-text h2 {
  font-size: 72px;
  margin: 0;
}

.parallax-text p {
  font-size: 18px;
  max-width: 50%;
  font-weight: bold;
}



